import React from 'react';
import { Card, CardImg, CardBody, CardTitle, Progress } from 'reactstrap';
import { PokemonPropTypes } from '../../lib/PokemonPropTypeValues';
import './Pokemon.css';

const defaultProps = {
  defeated: false,
  battle: false,
  onClick(event) {
    event.preventDefault();
  },
};

function hpColor(currentHp, totalHp) {
  if (currentHp <= totalHp * 0.2) {
    return 'danger';
  } else if (currentHp <= totalHp * 0.5) {
    return 'warning';
  }
  return 'success';
}

function Pokemon(props) {
  return (
    <Card onClick={props.onClick} className={props.defeated ? 'defeated' : ''}>
      <CardImg top width="100%" src={`${props.img}`} alt={props.name} />
      <CardBody>
        <CardTitle>{props.name}</CardTitle>

        {props.battle && (
          <div>
            <p>{props.level}</p>
            <Progress
              color={hpColor(props.currentHp, props.totalHp)}
              value={(props.currentHp / props.totalHp) * 100}
            >
              {props.currentHp}/{props.totalHp}
            </Progress>
          </div>
        )}
      </CardBody>
    </Card>
  );
}

Pokemon.propTypes = PokemonPropTypes.isRequired;
Pokemon.defaultProps = defaultProps;

export default Pokemon;



