import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Machine } from 'xstate';
import './App.css';
import pokebank from './pokemon.json';
import MusicPlayer from './components/MusicPlayer';
import SFXPlayer from './components/SFXPlayer';
import Instructions from './components/Instructions';
import Holster from './components/Holster';
import Arena from './components/Arena';

import battlehelpers from './utils/battleHelpers';

const battlerMachine = Machine({
  initial: 'starterselect',
  states: {
    starterselect: {
      on: {
        PICK: {
          challengerselect: {
            actions: ['addStarter'],
          },
        },
      },
      onExit: ['emitSFX'],
    },
    challengerselect: {
      on: {
        PICK: {
          battleround: {
            actions: ['addChallenger', 'calcHp'],
          },
        },
      },
      onExit: ['emitSFX'],
    },
    battleround: {
      on: {
        MOVE: {
          roundresult: {
            actions: ['calcDamage'],
          },
        },
      },
    },
    roundresult: {
      on: {
        WIN: 'battleresult',
        LOSE: 'end',
        NEXTTURN: 'battleround',
      },
    },
    battleresult: {
      on: {
        CONTINUE: 'challengerselect',
        CLEARED: 'end',
      },
    },
    end: {
      on: {
        RESET: 'starterselect',
      },
    },
  },
});

class App extends Component {
  constructor() {
    super();

    this.state = {
      battler: battlerMachine.initialState, // finite state
      entranceSFX: 0,
      pokebank,
      starter: {},
      challenger: {},
    };

    this.holsterHandler = this.holsterHandler.bind(this);
    this.arenaHandler = this.arenaHandler.bind(this);
  }
  /* eslint-disable consistent-return, class-methods-use-this */
  command(action, event) {
    console.log('action', action);
    console.log('event', event);
    switch (action) {
      case 'addStarter':
        console.log('case: addStarter action');
        if (event.starter) {
          return { starter: event.starter };
        }
        break;
      case 'emitSFX':
        console.log('case: emitSFX action');
        if (event.entranceId) {
          console.log(event.entranceId);
          return { entranceSFX: event.entranceId };
        }
        break;
      case 'addChallenger':
        console.log('case: addChallenger action');
        if (event.challenger) {
          return { challenger: event.challenger };
        }
        break;
      case 'calcHp':
        console.log('case: calcHp action');
        if (event.starter && event.challenger) {
          const { starter, challenger } = event;

          // set up hp for battle
          challenger.totalHp = battlehelpers.calcHp(challenger);
          challenger.currentHp = challenger.totalHp;

          // set up hp for battle
          starter.totalHp = battlehelpers.calcHp(starter);
          starter.currentHp = starter.totalHp;
          return { starter, challenger };
        }
        break;
      case 'calcDamage':
        console.log('case: determineResult');
        if (event.starter && event.challenger) {
          const { starter, challenger } = event;
          let dmg = battlehelpers.calcDamage(starter, challenger);
          challenger.currentHp = (dmg > challenger.currentHp) ? 0 : (challenger.currentHp - dmg);

          if (challenger.currentHp > 0) {
            dmg = battlehelpers.calcDamage(challenger, starter);
            starter.currentHp = (dmg > starter.currentHp) ? 0 : (starter.currentHp - dmg);
          }
          return { starter, challenger };
        }
        break;
      case 'resetCharacters':
        console.log('case: resetCharacters action');
        return { starter: {}, challenger: {} };
      default:
        break;
    }
  }
  /* eslint-disable consistent-return, class-methods-use-this */

  transition(event) {
    const currentBattlerState = this.state.battler;
    const nextBattlerState =
      battlerMachine.transition(currentBattlerState, event.type);
    console.log(`transitioning to this state next: ${nextBattlerState}`);
    console.log(`actions to do: ${nextBattlerState.actions}`);
    if (nextBattlerState.actions) {
      // const nextState = nextBattlerState.actions
      //   .reduce((state, action) => this.command(action, event) || state, undefined);
      const nextState = nextBattlerState.actions
        .reduce((state, action) => ({ ...state, ...this.command(action, event) } || state), {});
      console.log(`nextState: ${JSON.stringify(nextState)}`);
      console.log(`entering ${nextBattlerState.value}`);
      this.setState({
        battler: nextBattlerState,
        ...nextState,
      });
    }
  }

  determineRoundResult(starter, challenger) {
    console.log('determining...');
    if (challenger.currentHp === 0) {
      console.log('transition via WIN...');
      this.transition({
        type: 'WIN', starter, challenger,
      });
    } else if (starter.currentHp === 0) {
      console.log('transition via LOSE...');
      this.transition({
        type: 'LOSE', starter, challenger,
      });
    } else {
      console.log('transition via NEXTTURN...');
      this.transition({
        type: 'NEXTTURN', starter, challenger,
      });
    }
  }

  determineBattleResult() {
    for (let i = 0; i < this.state.pokebank.length; i += 1) {
      if (!this.state.pokebank[i].defeated) {
        return this.transition({ type: 'CONTINUE' });
      }
    }
    return this.transition({ type: 'CLEARED' });
  }

  arenaHandler() {
    console.log('arenaHandler');
    switch (this.state.battler.value) {
      case 'battleround':
        this.transition({
          type: 'MOVE',
          challenger: this.state.challenger,
          starter: this.state.starter,
        });
        break;
      case 'roundresult':
        this.determineRoundResult(this.state.starter, this.state.challenger);
        break;
      case 'battleresult':
        this.determineBattleResult();
        break;
      case 'end':
        this.transition({ type: 'RESET' });
        break;
      default:
        break;
    }
  }

  holsterHandler(pokemon) {
    // ignore pick if challenger has already been defeated
    if (pokemon.defeated) {
      console.warn(`${pokemon.name} already defeated`);
      return;
    }

    if (Object.keys(this.state.starter).length === 0) {
      console.log('calling this.transition()');
      this.transition({
        type: 'PICK',
        starter: pokemon,
        entranceId: pokemon.id,
      });
    } else if (Object.keys(this.state.challenger).length === 0) {
      console.log('calling this.transition()');
      this.transition({
        type: 'PICK',
        starter: this.state.starter,
        challenger: pokemon,
        entranceId: pokemon.id,
      });
    }
  }

  renderHolster(state) {
    if (state !== 'starterselect' && state !== 'challengerselect') return;

    return (<Holster
      disabled={state !== 'starterselect' && state !== 'challengerselect'}
      pokemon={this.state.pokebank.filter(p =>
        p.id !== this.state.starter.id)
      }
      clickHandler={this.holsterHandler}
    />);
  }

  renderArena(state) {
    if (state === 'starterselect' || state === 'challengerselect') return;

    return (
      <Arena
        state={state}
        starter={this.state.starter}
        challenger={this.state.challenger}
        arenaHandler={this.arenaHandler}
      />
    );
  }

  render() {
    const battlerState = this.state.battler;
    return (
      <Container className="App" data-state={battlerState.value}>
        <MusicPlayer state={battlerState.value} />
        <SFXPlayer pid={this.state.entranceSFX} />
        <h1>Poké-Battler</h1>
        <Row>
          <Col>
            <Instructions state={battlerState.value} />
          </Col>
        </Row>
        <Row>
          <Col>
            {this.renderHolster(battlerState.value)}
            {this.renderArena(battlerState.value)}
          </Col>
        </Row>
        {battlerState.value === 'end' &&
          <button
            onClick={() => this.transition({ type: 'RESET' })}
          >
            Reset
          </button>
        }
      </Container>
    );
  }
}

export default App;



