import React from 'react';
import PropTypes from 'prop-types';
import { CardGroup, Button } from 'reactstrap';
import { PokemonPropTypes } from './../lib/PokemonPropTypeValues';
import Pokemon from './Pokemon/Pokemon';

const propTypes = {
  state: PropTypes.string.isRequired,
  starter: PokemonPropTypes.isRequired,
  challenger: PokemonPropTypes.isRequired,
  arenaHandler: PropTypes.func.isRequired,
};

const ButtonText = {
  roundresult: 'Next',
  battleresult: 'Continue',
  end: 'Reset',
};

function Arena({
  state, starter, challenger, arenaHandler,
}) {
  return (
    <div className="arena">
      <CardGroup>
        <Pokemon {...starter} battle />
        <Pokemon {...challenger} battle />
      </CardGroup>
      <Button onClick={arenaHandler}>
        {state === 'battleround' ? starter.move.name : ButtonText[state]} &gt;
      </Button>
    </div>
  );
}

Arena.propTypes = propTypes;

export default Arena;



