import React from 'react';
import PropTypes from 'prop-types';

const propTypes = {
  state: PropTypes.string.isRequired,
};

const InstructionsText = {
  starterselect: 'Choose Your Starter Pokémon',
  challengerselect: 'Choose Your Challenger Pokémon',
  battleround: 'Battle!',
  end: 'Play Again?',
};

function Instructions({ state }) {
  return (
    <h3>{InstructionsText[state] || 'Battle!'}</h3>
  );
}

Instructions.propTypes = propTypes;

export default Instructions;



